const React = (window as any).React;
const antd = (window as any).antd;

const { Form, DatePicker, Button } = antd;

const TimeDifference = ({save_data}: {save_data: (data:any)=>void}) => {
  const [form] = Form.useForm();

  const calculateTimeDifference = () => {
    form.validateFields().then(values => {
      const selectedDateTime = values.datePicker.toDate();
      const currentDateTime = new Date();
      const differenceInSeconds = Math.abs((currentDateTime.getTime() - selectedDateTime.getTime()) / 1000);
      save_data(differenceInSeconds);
    });
  };

  return (
    <Form form={form} layout="inline">
      <Form.Item name="datePicker" rules={[{ required: true, message: '请选择日期和时间' }]}>
        <DatePicker showTime format="YYYY-MM-DD HH:mm:ss" />
      </Form.Item>
      <Form.Item>
        <Button type="primary" onClick={calculateTimeDifference}>
          计算时间差
        </Button>
      </Form.Item>
    </Form>
  );
};

export default TimeDifference;